.posts {
   padding: 5%;
}

.content__wrapper {
    margin-bottom: 60px;
}

.content__header-wrapper {
    background-color: $gray;
    padding: 3%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.content__header-wrapper h2 {
    color: $main-dark;
    font-family: 'Eastman';
    @include adaptiv-font(30, 30);
    font-weight: 700;
    margin-top: 0;
    margin-bottom: 10px;
}

.post__contentus img {
    flex: 0 0 70%;
}

.chapter__img {
    height: 400px;
    width: 100vw;
}

.content__heading {
    color: $main-dark;
    font-family: "Bebas Neue Cyrillic";
    @include adaptiv-font(90, 30);
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 10px;
}

.content__text {
    color: $main-dark;
    font-family: 'Eastman';
    @include adaptiv-font(25, 16);
    font-weight: 400;
    line-height: 1.5;
    margin-top: 0;
    margin-bottom: 20px;
    text-align: justify;
}

.content-list {
    color: $main-dark;
    font-family: 'Eastman';
    @include adaptiv-font(25, 16);
    font-weight: 400;
    line-height: 1.5;
    position: relative;
    padding: 0 0 0 50px;
    margin: 0 0 30px 0;
}

.content-list__item {
    margin-bottom: 20px;
    padding-left: 10px;
}

.content-list--dots li::marker {
    @include adaptiv-font(30, 16);
}

.business-img-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: 200px;
    grid-auto-rows: 50px;
    align-items: center;
    justify-content: center;
    grid-column-gap: 60px;
    grid-row-gap: 30px;
}

.business-img-list__item {
    display: flex;
    align-items: center;
    justify-self: space-between;
    height: 100%;

    & a {
        display: block;
        width: 100%;
        height: 100%;
    }

    & img {
        object-fit: contain;
    }
}

.posts-list {
    display: grid; 
    grid-template-columns: 1fr;
    margin-bottom: 40px;
    list-style: none;
    gap: 30px;
    margin-top: 0;
    padding: 0;
}

.posts {
    background-color: white;
}

.post__content {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;
    flex: 0 0 100%;
}

.more-btn {
    display: block;
    width: fit-content;
    font-family: "Eastman";
    @include adaptiv-font(23, 16);
    font-weight: 400;
    color: #0a0a0a;
    margin: 0 auto;
    text-decoration: none;
    padding: 10px 20px;
    border: 2px solid #0a0a0a;
    transition: all $transition;
}

.post__title {
    font-family: 'Eastman';
    @include adaptiv-font(20, 14);
    font-weight: 700;
    margin: 0 0 5px 0;

    & a {
        color: $main-dark;
        text-decoration: none;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        transition: all $transition;
    }
}

.post__date {
    color: #5D738D;
    font-family: 'Eastman';
    font-size: 14px;
    font-weight: 400;
}

.content__wrapper {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
}

.content__img {
    height: 400px;
}

.content {
    padding: 5% 5% 0 5%;
}

.chapter__content {
    padding: 5%;
}

.post__img {
    margin: 0;
    min-height: 50px;
    flex: 0 0 30%;
    overflow: hidden;

    & img {
        transition: transform $transition;
    }
}

.w100 {
    flex: 0 1 100%;                                                                         
}

