@media (min-width: 320px) {
    .posts-list {
      grid-template-columns: 1fr;
      grid-auto-rows: 100px;
      grid-template-rows: 400px; 
    }

    .post__content {
        flex-direction: row;
    }

    .current .post__content {
        flex-direction: column;
    }
}

@media (min-width: 768px) {
    .content {
        padding: $height 30px 5% 30px;
    }

    .posts {
        padding: calc(5% + $height) 5% 5% 5%;
    }

    .content__img {
        height: 600px;
    }

    .posts-list {
        grid-template-columns: repeat(2, 1fr);
        grid-auto-rows: 400px;
        grid-template-rows: 600px;
    }

    .current {
        grid-column-start: span 2;
    }

    .chapter__img {
        height: 600px;
    }

    .post__img {
        min-height: 320px;
        width: 100%;
    }

    .post__content {
        flex-direction: column;
    }

    .business-img-list {
        grid-template-columns: repeat(2, 200px);
        grid-row-gap: 36px;
        grid-auto-rows: 100px;
    }
}

@media (min-width: 1200px) {
    .business-img-list {
        grid-template-columns: repeat(3, 200px);
        grid-auto-rows: 200px;
    }

    .content__wrapper {
        display: flex;
        flex-direction: row;
        margin-bottom: 60px;
    }

    .chapter__img {
        height: 800px;
    }

    .content__header-wrapper {
        width: 40%;
    }
}

@media (min-width: 1440px) {
    .posts-list {
        grid-template-columns: repeat(3, 1fr);
    }

    .content {
        padding: $height 30px 5% 30px;
    }

    .posts {
        padding-top: calc(3% + $height);
        padding-bottom: 5%;
        padding-left: 0;
        padding-right: 0;
    }

    .posts-list {
        padding: 0 30px;
    }

    .h520 {
        min-height: 520px;
    }
}

@media (min-width: 1720px) {
    .chapter__img {
        width: 1440px;
        margin-left: auto;
        margin-right: auto;
        padding-left: 30px;
        padding-right: 30px;
    }
}

@media (any-hover: hover) {
    .more-btn:hover {
        background: $gradient;
        background-clip: text;
        color: transparent;
        border: 2px solid transparent;
        border-image: $gradient;
        border-image-slice: 1;
    }

    .post__title a:hover {
        background: $gradient;
        background-clip: text;
        color: transparent;
    }

    .post__img img:hover {
        transform: scale(1.1);
    }
}